<template>
    <div class="couponList">
        <div class="couponItem"  :class="couponItem.isShowScope?'couponItemMinMargin':'couponItemMaxMargin'"
        v-for="(couponItem,couponIndex) in lists" :key="'couponItem'+couponIndex">
            <div class="couponBg"></div>
            <div class="couponContent" :class="setCouponBg(couponItem)">
                <div class="couponPriceBox">
                    <div class="couponPrice">
                        <div class="couponPriceIcon">￥</div>
                        <div class="couponPriceNum">{{couponItem.couponTotal}}</div>
                    </div>
                    <div class="couponPriceTxt" v-show="couponItem.limitTotal!=0">满{{couponItem.limitTotal}}元可用</div>
                    <div class="couponPriceTxt" v-show="couponItem.limitTotal==0">无门槛</div>
                </div>
                <div class="couponInfoBox">
                    <div class="couponInfo">
                        <div class="couponName" v-show="couponItem.type!='02'">{{couponItem.name}}</div>
                        <div class="couponName" v-show="couponItem.type=='02'">{{couponItem.shopName}}优惠券</div>
                        <div class="couponTime" v-if="couponItem.effType == '1'||couponItem.effBeginDay">{{couponItem.effBeginDay}}-{{couponItem.effEndDay}}</div>
                        <div class="couponTime" v-if="couponItem.effType == '2'&&couponItem.effDays == 1&&!couponItem.effBeginDay">
                            领取后当天有效
                        </div>
                        <div class="couponTime" v-if="couponItem.effType == '2'&&couponItem.effDays != 1&&!couponItem.effBeginDay">
                            领取后{{couponItem.effDays}}天内有效
                        </div>
                        <div class="couponScope" @click="showAndHideScope(couponItem)">
                            <div class="couponScopeTxt">适用范围</div>
                            <img v-show="couponItem.type!='02'" :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon" src="@/assets/images/newSlicing/coupon/white_arrow_down_cut.png" alt="">
                            <img v-show="couponItem.type=='02'" :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon" src="@/assets/images/newSlicing/coupon/arrow_down_cut.png" alt="">
                        </div>
                    </div>
                    <div class="couponBtnBox">
                        <div class="couponBtn" v-if="(couponItem.isPicked==1||couponItem.status=='01')&&lists.isShowBtn" @click="goToShopPage(couponItem)">立即使用</div>
                        <div class="couponBtn" v-if="couponItem.isPicked==0&&couponItem.isFinished==0" @click="receiveBtn(couponItem)">立即领取</div>
                    </div>
                </div>
            </div>
            <div class="couponTypeBox" v-show="couponItem.isShowScope">
                <div class="couponTypeTxt">
                    <div>{{couponItem.shopScope}}</div>
                </div>
<!--                <div class="couponTypeTxt" v-if="couponItem.couponShopList!=null">-->
<!--                    <span v-if="couponItem.couponShopList[0]">{{couponItem.couponShopList[0].shopName}}</span>-->
<!--                    <span v-show="couponItem.couponShopList.length>=2">、</span>-->
<!--                    <span v-if="couponItem.couponShopList[1]">{{couponItem.couponShopList[1].shopName}}</span>-->
<!--                    <span v-show="couponItem.couponShopList.length>2">等</span>-->
<!--                    <span>{{couponItem.couponShopList.length}}个</span>全品类通用-->
<!--                </div>-->
            </div>
        </div>
    </div>
</template>

<script>
    import { receiveBtnService } from '../../services/goodsService.js';
    import { Toast, Dialog } from "vant";
    export default {
        name: "couponItem",
        data(){
            return{
                // isShowScope:false,
            }
        },
        props: {
            lists: {
                type: Array,
                default: () => []
            },
        },
        mounted(){
            console.log(this.lists)
            this.lists.map((couponItem)=>{
                couponItem.isShowScope = false;
            })
        },
        activated(){

        },
        deactivated(){

        },
        watch:{

        },
        computed: {
            //设置优惠券背景图片
            setCouponBg(){
                return function(couponItem){
                    if(couponItem.type=='02'){
                        if(couponItem.status){
                            if(couponItem.status=='01'){
                                return 'stayCouponBg';
                            }else if(couponItem.status=='02'){
                                return 'beenUsedCouponBg';
                            }else{
                                return 'pastdueCouponBg';
                            }
                        }else{
                            if(couponItem.isFinished==1&&couponItem.isPicked!=1){
                                return 'staleCouponBg';
                            } else if(couponItem.isPicked==1){
                                return 'getCouponBg';
                            }else {
                                return 'stayCouponBg';
                            }
                        }

                    }else{
                        if(couponItem.status){
                            if(couponItem.status=='01'){
                                return 'terraceBg';
                            }else if(couponItem.status=='02'){
                                return 'beenUsedTerraceBg';
                            }else{
                                return 'pastdueTerraceBg';
                            }
                        }else{
                            if(couponItem.isFinished==1&&couponItem.isPicked!=1){
                                return 'staleTerraceBg';
                            } else if(couponItem.isPicked==1){
                                return 'getTerraceBg';
                            }else {
                                return 'terraceBg';
                            }
                        }
                    }
                }

            }
        },
        methods:{
            /**
             * 领取优惠券
             * @param
             * id        Number      优惠券CODE
             * ownerCode Number      用户CODE
             * areaCode  Number      小区CODE
             **/
            receiveBtn(couponItem) {
                let _this = this;
                if (!this.$userInfo.ROOM_CODE) {
                    Dialog.confirm({
                        title: "您未绑定房间，是否前往绑定？",
                        confirmButtonText: "前往"
                    }).then(() => {
                        //跳转到绑定房产页面
                        this.$router.push("selectArea");
                    }).catch(() => {
                    });
                    return
                }
                let paramData = {
                    id: couponItem.id,
                    ownerCode: this.$userInfo.OWNER_CODE,
                    areaCode: this.$userInfo.AREA_CODE,
                    roomCode: this.$userInfo.ROOM_CODE
                }
                receiveBtnService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        Toast("领取优惠券成功");
                        couponItem.isPicked = 1;
                        // _this.queryPickupListByItem(couponItem);
                    } else {
                        Toast(res.msg);
                    }
                }).catch(error => {
                    Toast(error)
                });
                //
            },
            //跳转优惠券对应店铺
            goToShopPage(couponItem) {
                if(couponItem.type=='01'){
                    let shopCodeList = [];
                    this.$router.push({
                        name: "goodsCollection",
                        params: {
                            couponName:couponItem.limitTotal!=0?`满${couponItem.limitTotal}减${couponItem.couponTotal}`:`无门槛立减${couponItem.couponTotal}元`,
                            shopCodeList:shopCodeList,
                        }
                    });
                }else if(couponItem.type=='02'){
                    this.$router.push({ name: "shopPage", params: { shopCode: couponItem.shopCode } });
                }else{
                    let shopCodeList = "";
                    couponItem.couponShopList.map((shopItem,shopIndex)=>{
                        if(shopIndex==0){
                            shopCodeList = shopItem.shopCode;
                        }else{
                            shopCodeList = shopCodeList+','+ shopItem.shopCode;
                        }
                    })
                    this.$router.push({
                        name: "goodsCollection",
                        params: {
                            couponName:couponItem.limitTotal!=0?`满${couponItem.limitTotal}减${couponItem.couponTotal}`:`无门槛立减${couponItem.couponTotal}元`,
                            shopCodeList:shopCodeList,
                        }
                    });
                }
            },
            //查看适应范围
            showAndHideScope(couponItem){
                console.log(couponItem)
                couponItem.isShowScope = !couponItem.isShowScope;
                this.$forceUpdate();
            },
        },
    }
</script>

<style scoped lang="scss">

    .couponList{
        padding: 0 15px;
        .couponItem{
            /*max-height: 114.5px;*/
            position: relative;
            .couponBg{
                width: 345px;
                height: 90px;
            }
        }
        .couponItemMaxMargin{
            margin-bottom: 15px;
        }
        .couponItemMinMargin{
            margin-bottom: 3.5px;
        }
        .couponContent{
            width: 345px;
            height: 90px;
            position: absolute;
            top: 0;
            left: 0;

            display: flex;
            justify-content: space-between;
            z-index: 2;
            .couponPriceBox{
                width: 100px;
                height: 90px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .couponPrice{
                    display: flex;
                    align-items: flex-end;
                    justify-content: center;
                    .couponPriceIcon{
                        font-family: Barlow-ExtraBold;
                        font-size: 16px;
                        color: #FFFFFF;
                        letter-spacing: 0;
                        line-height: 15px;
                    }
                    .couponPriceNum{
                        font-family: Barlow-ExtraBold;
                        font-size: 28px;
                        color: #FFFFFF;
                        letter-spacing: 1px;
                        line-height: 25px;
                        font-weight: 600;
                    }
                }
                .couponPriceTxt{
                    ont-family: PingFangSC-Regular;
                    font-size: 11px;
                    color: #FFFFFF;
                    letter-spacing: 0;
                    margin-top: 5px;
                }
            }
            .couponInfoBox{
                width: 245px;
                height: 90px;
                display: flex;
                .couponInfo{
                    width: 149px;
                    height: 90px;
                    margin-left: 10px;
                    .couponName{
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        font-family: PingFangSC-Medium;
                        font-size: 14px;
                        color: #FFFFFF;
                        letter-spacing: 0;
                        line-height: 20px;
                        margin-top: 10px;
                        font-weight: 600;
                    }
                    .couponTime{
                        font-family: PingFangSC-Regular;
                        font-size: 11px;
                        color: #FFFFFF;
                        letter-spacing: 0;
                        line-height: 15px;
                    }
                    .couponScope{
                        margin-top: 27px;
                        display: flex;
                        align-items: center;
                        .couponScopeTxt{
                            font-family: PingFangSC-Regular;
                            font-size: 9px;
                            color: #FFFFFF;
                            letter-spacing: 0;
                        }
                        .couponScopeIcon{
                            width: 7px;
                            height: 4px;
                            margin-left: 3px;
                        }
                        .rotateImg{
                            transform: rotate(180deg);
                        }
                    }
                }
                .couponBtnBox{
                    margin-left: 6px;
                    .couponBtn{
                        width: 66px;
                        height: 28px;
                        margin-top: 31px;
                        background: #FFFFFF;
                        box-shadow: 0 0 10px 0 rgba(218,56,56,0.55);
                        border-radius: 200px;
                        font-family: PingFangSC-Medium;
                        font-size: 12px;
                        color: #FB5768;
                        letter-spacing: 0;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }
        }
        /*平台背景图片*/
        .terraceBg{
            background: url("../../assets/images/newSlicing/coupon/pt_coupon_cut.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .getTerraceBg{
            background: url("../../assets/images/newSlicing/coupon/pt_coupon_cut2.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .staleTerraceBg{
            background: url("../../assets/images/newSlicing/coupon/pt_coupon_cut3.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .pastdueTerraceBg{
            background: url("../../assets/images/newSlicing/coupon/pt_coupon_cut4.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .beenUsedTerraceBg{
            background: url("../../assets/images/newSlicing/coupon/pt_coupon_cut5.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        /*优惠券背景图片*/
        .stayCouponBg{
            background: url("../../assets/images/newSlicing/coupon/dp_coupon_cut.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .getCouponBg{
            background: url("../../assets/images/newSlicing/coupon/dp_coupon_cut2.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .staleCouponBg{
            background: url("../../assets/images/newSlicing/coupon/dp_coupon_cut3.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .pastdueCouponBg{
            background: url("../../assets/images/newSlicing/coupon/dp_coupon_cut4.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .beenUsedCouponBg{
            background: url("../../assets/images/newSlicing/coupon/dp_coupon_cut5.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
        }
        .couponTypeBox{
            width: 345px;
            min-height: 36px;
            background: #FFFFFF;
            box-shadow: 0 2px 4px 0 rgba(226,226,226,0.50);
            position: relative;
            top: -11.5px;
            z-index: 1;
            border-radius: 0 0 10px 10px;
            .couponTypeTxt{
                width: 315px;
                margin: 0 auto;
                padding: 15.5px 0 4.5px 0;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #999999;
                letter-spacing: 0;
                div{
                    max-height: 32px;
                    line-height: 16.5px;
                    display: -webkit-box;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
            }
        }

        .stayCouponBg.couponContent,.getCouponBg.couponContent,.staleCouponBg.couponContent,.pastdueCouponBg.couponContent,.beenUsedCouponBg.couponContent{
            .couponInfoBox{
                .couponInfo{
                    .couponName{
                        color: #333333;
                    }
                    .couponTime{
                        color: #666666;
                    }
                    .couponScope{
                        .couponScopeTxt{
                            color: #999999;
                        }
                    }
                }
            }
            .couponBtnBox{
                .couponBtn{
                    background: #FB5768;
                    box-shadow: 0 0 10px 0 rgba(207,207,207,0.55);
                    border-radius: 200px;
                    color: #FFFFFF;
                }
            }
        }
    }
</style>
